<template>
  <div class="storedetail">
      <h1 class="large_t">中山园路店</h1>
      <img src="./store.png" class="banner" alt="">
      <div>
          <p class="two_t_m">地址：</p>
          <p class="two_t_h">深圳市南山区中山园路 TCL 国际 E 城 1008 得乐 Café</p>
      </div>
      <div class="time">
          <p class="two_t_m">营业时间：</p>
          <p class="two_t_h">10:00～22:00（周一至周五）<br>
            10:30～23:00（周六、周日）
          </p>
      </div>
      <div>
          <p class="two_t_m">门店电话：</p>
          <p class="two_t_h">0755-88886666</p>
      </div>
  </div>
</template>

<script>
export default {
  name: "Storedetail",
  data() {
    return {

    };
  },
  methods: {

  },
  components: {}
};
</script>

<style scoped lang="less">
.storedetail {
    font-size: 0.16rem;
    .banner {
        width: 3.2rem;
        height: 1.7rem;
        border-radius: 0.12rem;
        margin: 0.15rem 0rem;
    }
    .time {
        padding: 0.15rem 0rem;
        margin: 0.15rem 0rem;
        border-top: 0.01rem solid #f2f2f2;
        border-bottom: 0.01rem solid #f2f2f2
    }
    p {
        &:nth-child(1){
            margin-bottom: 0.05rem;
        }
    }
}
</style>



